import React from "react";
import "./Footer.css";
import footerimd from "../images/Asset.png";
import { Container, Grid, Typography, useTheme } from "@mui/material";
import { Box } from "@mui/system";
import useMediaQuery from "@mui/material/useMediaQuery";
// icons mui
import FacebookIcon from "@mui/icons-material/Facebook";
import YouTubeIcon from "@mui/icons-material/YouTube";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";
const Footer = () => {
  const theme = useTheme();
  const matchessm = useMediaQuery(theme.breakpoints.down("sm"));
  const matchesmd = useMediaQuery(theme.breakpoints.up("md"));
  const matcheslg = useMediaQuery(theme.breakpoints.down("lg"));

  const styles = {
    fontSize: { sm: "", md: "", lg: "11px" },
    textAlign: { sm: "center", md: "end", lg: "center" },
    bony: "#876e3a",
    p: "#a3a3a3",
  };

  return (
    <>
      <div className="Footer  footertet ">
        <div className="fooooooter  py-5">
          <div className="container ">
            <div className="row ">
              <div className="text-sm-center  mt-lg-0 mt-sm-5 col-lg-12 col-md-12  ">
                <img src={footerimd} className="pt-5" />
                <Typography
                  fontSize={{ lg: "1.500rem", md: "1.100rem", sm: "1.400rem" }}
                  fontWeight={"bold"}
                  textAlign={"center"}
                  p={1}
                  mt={1}
                >
                  مدارس انجال الصفوة الاهلية © جميع الحقوق محفوظة
                </Typography>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Footer;
