import React, { useRef, useState } from "react";
import { Navigation, Pagination } from "swiper";
import "./Slider.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
// icons image
import iacon1 from "../images/ayggg.png";
import iacon4 from "../images/aygg4.png";
import iacon2 from "../images/aygg3.png";

const Sliderimge = () => {
  return (
    <>
      <div className=" py-5 text-white">
        <div className="container">
          <div className="row">
            <Swiper
              breakpoints={{
                320: { slidesPerView: 1, spaceBetween: 30 },
                768: { slidesPerView: 2, spaceBetween: 30 },
                1024: { slidesPerView: 3, spaceBetween: 30 },
              }}
              loop={true}
              spaceBetween={30}
              pagination={{
                clickable: true,
              }}
              navigation={true}
              modules={[Pagination, Navigation]}
              className="mySwiper col-sm-12  "
            >
              <div className="row">
                <div
                  class="uk-position-relative uk-visible-toggle uk-light"
                  tabindex="-1"
                  uk-slider="sets: true"
                >
                  <ul class=" hi uk-slider-items uk-child-width-1-2 uk-child-width-1-3@m uk-grid">
                    <SwiperSlide className="hoverall">
                      <div className="boxone">
                        <div class="faceone frontone ">
                          <div className="boxfrontone">
                            <img src={iacon1} />
                            <h3>الرحلات </h3>
                          </div>
                        </div>
                        <div class="faceone backone">
                          <h3>الرحلات</h3>
                          <p className="container">
                            تهدف الرحلات لدينا الى زيادة العلاقات المجتمعية بين
                            اسرتنا وزيادة المحبة والود بيننا ومن ثم تحفيز الطالب
                            لدينا على الاستمرار في العطاء والمثابرة بين خلانه
                          </p>
                        </div>
                      </div>
                    </SwiperSlide>
                    <SwiperSlide className="hoverall">
                      <div className="boxtow">
                        <div class="facetow fronttow ">
                          <div className="boxfronttow">
                            <img src={iacon4} />
                            <h3> الثقة بالنفس</h3>
                          </div>
                        </div>
                        <div class="facetow backtow">
                          <h3> الثقة بالنفس</h3>
                          <p className="container">
                            نقوم على زيادة ثقة الفرد بنفسه من خلال المشاركة في
                            الانشطة المختلفة التي يقيمها ادارة المدرسة لدينا
                          </p>
                        </div>
                      </div>
                    </SwiperSlide>
                    <SwiperSlide className="hoverall">
                      <div className="boxtree">
                        <div class="facetree fronttree ">
                          <div className="boxfronttree">
                            <img src={iacon2} />
                            <h3> تأسيس الجيل الصاعد</h3>
                          </div>
                        </div>
                        <div class="facetree backtree">
                          <h3> تأسيس الجيل الصاعد</h3>
                          <p className="container">
                            تاسيس الجيل الصاعد في اخلاقه واهدافه التي يستفيد
                            منها المجتمع من خلال العطاء والمثابرة التي يخرج بها
                            الطالب في مدارس الصفوة
                          </p>
                        </div>
                      </div>
                    </SwiperSlide>
                    <SwiperSlide className="hoverall">
                      <div className="boxfour">
                        <div class="facefour frontfour ">
                          <div className="boxfrontfour">
                            <img src={iacon1} />
                            <h3> الادارة الناجحة </h3>
                          </div>
                        </div>
                        <div class="facefour backfour">
                          <h3> الادارة الناجحة </h3>
                          <p className="container">
                            يتم عمل اجتماع اسبوعي لدينا في طاقم الادارة نستحدث
                            فيه الانشطة ونرسخ امكانياتنا فيها بكل ما اوتينا من
                            عمل وجهد للنزول بها الى الطالب بشكل منظم ومهني
                          </p>
                        </div>
                      </div>
                    </SwiperSlide>
                  </ul>
                </div>
              </div>
            </Swiper>
          </div>
        </div>
      </div>
    </>
  );
};

export default Sliderimge;
