import React from "react";
import image2 from "../images/11111.png";
import "./Slider.css";
import Sliderimge from "./Sliderimge";
import AOS from "aos";
import "aos/dist/aos.css";
import Time from "../taotheg/Timer";
const Slider = () => {
  AOS.init();
  return (
    <div className="homeslier  pt-5" id="Slider">
      <div className="Slider" id="Ma3rad">
        <div className="container">
          <div className="imgsliderfog">
            <div data-aos="fade-right">
              <img src={image2} />
            </div>
          </div>
          <div data-aos="fade-right" className="row dir text-end">
            <div className="col-lg-4 col-md-12 text-white">
              <h1> معرض الصور</h1>
            </div>
            <div className="col-lg-8 col-sm-12">
              <div>
                <p className="mt-3  text-secondary fw-bold pcorol">
                  نوفر في مدارس انجال الصفوة الاهلية الكثير من الامتيازات
                  والتخصصات التي تساعد طفلك على الوصول الى هدفه والتحسين من
                  قدراته العقلية والجسدية والقيادية حتى ، من خلال تنمية الحس
                  الذهني والاجتماعي لديه بالنشاطات الترفيهية والمسابقات وغيرها
                  من الامور التي تهدف المنشأة الى تحقيقها
                </p>
              </div>
            </div>
          </div>
          <div data-aos="fade-up" data-aos-duration="3000">
            <Sliderimge />
          </div>
        </div>
        <Time />
      </div>
    </div>
  );
};

export default Slider;
