import { useEffect, useState } from "react";
import {
  Looding,
  Angazat,
  About,
  Footer,
  Home,
  Slider,
  Taotheg,
} from "./componets";

function App() {
  const [looding, setlooding] = useState(false);
  useEffect(() => {
    setlooding(true);
    setTimeout(() => {
      setlooding(false);
    }, 7000);
  }, []);

  return (
    <>
      {looding ? (
        <Looding />
      ) : (
        <div>
          <Home /> <About /> <Angazat /> <Taotheg /> <Slider />
          <Footer />
        </div>
      )}
    </>
  );
}

export default App;
