import React from "react";
import "./Home.css";
import image from "../images/Asset.png";
import AnimatedCursor from "react-animated-cursor";
import { Box, Typography } from "@mui/material";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import Tooltip from "@mui/material/Tooltip";
const Home = () => {
  return (
    <>
      <Box
        onClick={() =>
          window.open("https://api.whatsapp.com/send/?phone=966558048004")
        }
        sx={{
          position: "fixed",
          bottom: 32,
          right: 32,
          cursor: "pointer",
          zIndex: "111",
        }}
      >
        <Tooltip
          title={
            <Typography variant="h5" sx={{ fontWeight: "bold" }}>
              ! تواصل معي الان
            </Typography>
          }
          placement="left"
        >
          <WhatsAppIcon
            sx={{
              justifyContent: "center",
              fontSize: "70px",
              color: "#30b945",
              transition: " 1s",
              ":hover": {
                color: "yellowgreen",
              },
            }}
          />
        </Tooltip>
      </Box>
      <AnimatedCursor
        innerSize={8}
        outerSize={25}
        color="743, 74, 111"
        outerAlpha={0.2}
        innerScale={0.7}
        outerScale={2}
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
        ]}
      />

      <div className="Home">
        <div className="allnav">
          <div className="imgnave">
            <img src={image} className="imgg img-fluid d-flex  " />
          </div>

          <div className="fw-bold fontnav">
            <ul className="flex  ">
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a
                    href="https://forms.gle/rpyEQ8gccoM2ZSyU9"
                    style={{ color: "white" }}
                  >
                    رابط التسجيل
                  </a>
                </polygon>
              </li>

              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Angazat" style={{ color: "white" }}>
                    ابرز الانجازات
                  </a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Ma3rad" style={{ color: "white" }}>
                    معرض الصور
                  </a>
                </polygon>
              </li>
              <li className="fontnavvv ">
                <polygon className="my-2">
                  <a
                    href="https://www.anjalalsifwa.com/"
                    style={{ color: "#AAE800" }}
                  >
                    الموقع الرئيسي
                  </a>
                </polygon>
              </li>
            </ul>
          </div>
        </div>
        <div className="all-text-home">
          <div className="my-3 ">
            <h1 style={{ fontFamily: "Reem Kufi" }} className="my-4 h1h1">
              مدارس
            </h1>
            <h1 className="h1h1"> انجال الصفوة الأهلية</h1>
          </div>
          <div className="my-5">
            <p className="le">
              القيم و الأخلاق | الابتكار والابداع | تنمية المهارات{" "}
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
