import React, { useState, useEffect, useRef } from "react";

const Time = () => {
  const [timer, setTimer] = useState({
    days: "00",
    hours: "00",
    minutes: "00",
    seconds: "00",
  });
  const Ref = useRef(null);

  const getTimeRemaining = (e) => {
    const total = Date.parse(e) - Date.parse(new Date());
    const seconds = Math.floor((total / 1000) % 60);
    const minutes = Math.floor((total / 1000 / 60) % 60);
    const hours = Math.floor((total / 1000 / 60 / 60) % 24);
    const days = Math.floor(total / (1000 * 60 * 60 * 24)); // حساب الأيام
    return { total, days, hours, minutes, seconds };
  };

  const startTimer = (e) => {
    const { total, days, hours, minutes, seconds } = getTimeRemaining(e);
    if (total >= 0) {
      setTimer({
        days: days > 9 ? days : "0" + days,
        hours: hours > 9 ? hours : "0" + hours,
        minutes: minutes > 9 ? minutes : "0" + minutes,
        seconds: seconds > 9 ? seconds : "0" + seconds,
      });
    }
  };

  const clearTimer = (e) => {
    setTimer({ days: "00", hours: "00", minutes: "00", seconds: "10" }); // إعادة تعيين العداد
    if (Ref.current) clearInterval(Ref.current);
    Ref.current = setInterval(() => {
      startTimer(e);
    }, 1000);
  };

  const getDeadTime = () => {
    const now = new Date();
    const nextSaturday = new Date();

    // حساب تاريخ السبت القادم
    nextSaturday.setDate(now.getDate() + ((6 - now.getDay() + 7) % 7 || 7));

    // إضافة أسبوع (7 أيام)
    nextSaturday.setDate(nextSaturday.getDate() + 7);

    return nextSaturday;
  };

  useEffect(() => {
    clearTimer(getDeadTime());
    return () => clearInterval(Ref.current); // تنظيف المؤقت عند التفكيك
  }, []);

  return (
    <div style={{ textAlign: "center", margin: "auto", color: "#AAE800" }}>
      <h1 style={{ color: "green", paddingBottom: 2 }}>
        الوقت المتبقي للعد التنازلي
      </h1>

      <h2>
        {timer.days} <span style={{ color: "white" }}> days </span>
        {timer.hours} <span style={{ color: "white" }}> hours </span>
        {timer.minutes} <span style={{ color: "white" }}> minutes </span>
        {timer.seconds} <span style={{ color: "white" }}> seconds </span>
      </h2>
    </div>
  );
};

export default Time;
