import React, { useEffect, useState } from "react";
import { Box } from "@mui/material";
import LinearProgress from "@mui/material/LinearProgress";
import "./index.css";
import imagloooding from "./Companet/images/Asset.png";
import "animate.css";
import Time from "./Companet/taotheg/Timer";

export default function Looding() {
  const [looding, setlooding] = useState(false);
  useEffect(() => {
    setlooding(true);
    setTimeout(() => {
      setlooding(false);
    }, 7000);
  }, []);

  return (
    <>
      {looding && (
        <div>
          <Box sx={{ width: "100%" }}>
            <LinearProgress color="inherit" sx={{ color: "#665D5A" }} />
          </Box>
          <div className="alllogding">
            <div className="imglogdin animate__bounceInUp">
              <div className="text-center">
                <img src={imagloooding} width={"250px"} />
              </div>
              <div className="pt-5">
                <Time />
              </div>
            </div>
            <div className="lodbac"></div>
          </div>
        </div>
      )}
    </>
  );
}
